.content {
  &__tabs {
    margin: 48px 0;
  }

  &__tab {
    display: none;
  }

  &__tab + label {
    border-bottom: 2px solid $grey-border;
    width: 45%;
    cursor: pointer;
    display: inline-block;
    padding-bottom: 8px;
    margin: 0 12px 0 0;
  }

  &__tab:checked + label {
    color: $blue;
    border-bottom: 2px solid $blue;
    position: relative;

    &::after {
      position: absolute;
      bottom: -10px;
      left: calc(50% - 10px);
      display: block;
      content: "";
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid $blue;
    }
  }

  &__tab-description {
    display: none;
    box-sizing: border-box;
    padding: 48px 0 0 0;

    & > h3 {
      @include title-h3;
    }
  }

  &__tab--kontaktor:checked ~ .content__tab-description--kontaktor,
  &__tab--client:checked ~ .content__tab-description--client {
    display: block;
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    margin: 32px 0;

    &__tab + label {
      text-align: center;
    }
  }
}
