.content {
  &__support {
    margin: 32px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__support-description {
    min-width: 60%;
    margin: 0 20px 0 0;

    h3,
    p {
      @include title-h3;
    }

    p {
      font-weight: normal;
    }

    h3 {
      font-weight: 700;
      margin: 0 0 48px;
    }
  }

  &__support-img {
    width: 100%;
    max-width: 339px;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__support-advantages {
    font-weight: 700;
    color: $blue;
  }

  &__support-button {
    width: 260px;
    border-color: $blue;
    color: $white;
    background-color: $blue;
    margin: 60px 0 0;
    padding: 18px;
    border-radius: 28px;
    font-family: $Roboto;
    font-size: 16px;
    font-weight: 700;

    &:hover {
      border-color: $grey-border;
      color: $blue;
      background-color: $white;
    }
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    &__support {
      margin: 32px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &__support-description {
      margin: 0 10px 0 0;
    }

    &__support-img {
      width: 100%;
      max-width: 339px;

      img {
        width: 100%;
        height: auto;
      }
    }

    &__support-advantages {
      font-weight: 700;
      color: $blue;
    }

    &__support-button {
      width: 260px;
      border-color: $blue;
      color: $white;
      background-color: $blue;
      margin: 60px 0 0;
      padding: 14px;
      border-radius: 28px;

      &:hover {
        border-color: $grey-border;
        color: $blue;
        background-color: $white;
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    &__support-img {
      display: none;
    }

    &__support-button {
      width: 100%;
    }
  }
}
