.content {
  &__form {
    position: relative;
    width: 720px;
    background-color: $white;
  }

  &__form-overlay {
    position: fixed;
    z-index: 10000;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(33, 37, 41, 0.8);
    display: flex;
    justify-content: center;
    overflow: auto;

    &--hide {
      display: none;
    }
  }

  &__form-close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    background-color: transparent;
    background-image: url("../img/close-icon.svg");
    background-size: cover;
    border: none;
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    &__info-item {
      img {
        width: 124px;
        height: 124px;
      }

      &:not(:last-child) {

        &::after {
          top: 54px;
          right: -33px;
          width: 40px;
          height: 18px;
          background-size: cover;
        }
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    &__info-list {
      flex-direction: column;
    }

    &__info-item {
      text-align: center;
      width: 100%;
      margin: 0 auto 64px;

      img {
        width: 164px;
        height: 164px;
      }

      p {
        margin: 12px 0 0;
        font-size: 1em;
      }

      &:not(:last-child) {
        position: relative;

        &::after {
          position: absolute;
          top: unset;
          bottom: -36px;
          right: calc(50% - 32px);
          display: block;
          content: "";
          width: 60px;
          height: 22px;
          background-image: url("../img/info-arrow.svg");
          transform: rotate(90deg);
        }
      }
    }
  }
}
