.content {
  &__info-list {
    @include list;
    display: flex;
    justify-content: space-between;
  }

  &__info-item {
    text-align: center;
    width: 30%;

    img {
      width: 164px;
      height: 164px;
    }

    p {
      margin: 16px 0 0;
      font-size: 0.78em;
    }

    &:not(:last-child) {
      position: relative;

      &::after {
        position: absolute;
        top: 71px;
        right: -48px;
        display: block;
        content: "";
        width: 60px;
        height: 22px;
        background-image: url("../img/info-arrow.svg");
      }
    }
  }

  &__info-button {
    padding: 14px;
    width: 260px;
    margin: 40px auto 0;
    background-color: $blue;
    border: 1px solid $blue;
    color: $white;

    &:hover {
      background-color: $white;
      border: 1px solid $grey-border;
      color: $blue;
    }
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    &__info-item {
      img {
        width: 124px;
        height: 124px;
      }

      &:not(:last-child) {

        &::after {
          top: 54px;
          right: -33px;
          width: 40px;
          height: 18px;
          background-size: cover;
        }
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    &__info-list {
      flex-direction: column;
    }

    &__info-item {
      text-align: center;
      width: 100%;
      margin: 0 auto 64px;

      img {
        width: 164px;
        height: 164px;
      }

      p {
        margin: 12px 0 0;
        font-size: 1em;
      }

      &:not(:last-child) {
        position: relative;

        &::after {
          position: absolute;
          top: unset;
          bottom: -36px;
          right: calc(50% - 32px);
          display: block;
          content: "";
          width: 60px;
          height: 22px;
          background-image: url("../img/info-arrow.svg");
          transform: rotate(90deg);
        }
      }
    }
  }
}
