.content {
  &__table {
    margin: 40px 0 0;
  }

  &__table-string {
    @include list;
    display: flex;
    justify-content: space-between;

    &--title {
      .content__table-item {
        color: $blue;
        padding: 5px 15px 20px;
        border-right: 1px solid $blue;
        border-bottom: 1px solid $blue;
        text-align: center;

        &:last-child {
          border-right: none;
        }
      }
    }
  }

  &__table-data {
    @include list;
  }

  &__table-item {
    box-sizing: border-box;
    padding: 16px 0;
    text-align: center;

    &--series {
      width: 28%;
      text-align: left;
      padding: 16px 0 16px 20px;
    }

    &--time {
      width: 25%;
    }

    &--warranty {
      width: 25%;
    }

    &--link {
      width: 22%;
    }
  }

  &__table-data-item {
    width: 100%;

    &:nth-child(odd) {
      background-color: $grey-background;
    }
  }

  &__table-item-button {
    width: 50%;
    font-size: 0.7em;
    color: $blue;
    border: 1px solid $blue;
    border-radius: 10px;
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    &__table-string {
      &--title {
        .content__table-item {
          padding: 5px 10px 15px;
        }
      }
    }

    &__table-item {
      word-wrap: break-word;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    &__table {
      font-size: 0.85em;
    }

    &__table-string {
      &--title {
        .content__table-item {
          padding: 5px 2px 10px;
        }
      }
    }

    &__table-item {
      padding: 12px 0;

      &--series {
        width: 28%;
        padding: 12px 0;
      }

      &--time {
        width: 25%;
      }

      &--warranty {
        width: 25%;
      }

      &--link {
        width: 22%;
      }
    }

    &__table-item-button {
      width: 100%;
      font-size: 1em;
    }
  }
}
