@mixin title-h1 {
  font-family: $Roboto;
  font-weight: 700;
  font-size: 2em;
  line-height: 155%;
  margin: 0;
}

@mixin title-h2 {
  font-weight: 700;
  font-size: 1.55em;
  line-height: 155%;
  margin: 0;
}

@mixin title-h3 {
  font-family: $Roboto;
  font-weight: 500;
  font-size: 1.33em;
  line-height: 155%;
  margin: 0;
}

@mixin list {
  list-style: none;
  margin: 0;
  padding: 0;
}
