.button {
  text-align: center;
  display: block;
  box-sizing: border-box;
  background-color: $white;
  border: 1px solid $grey-border;
  border-radius: 24px;
  margin: 0 auto;
  cursor: pointer;
  text-decoration: none;

  &:hover {
    border: 1px solid $blue;
    background-color: $blue;
    color: $white;
  }
}
