.content {
  &__schedule-list {
    @include list;
    margin: 40px 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  &__schedule-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 42%;
    box-sizing: border-box;
    padding: 15px;
    background-color: $blue;
    color: $white;
    margin: 0 0 32px;

    &:nth-child(2n) {
      margin: 0 0 32px 32px;
    }

    &:hover {
      background-color: $blue-background;
    }
  }

  &__schedule-item-date {
    @include title-h2;
    width: 100px;
    text-align: center;
    border-bottom: 1px solid $white;
  }

  &__schedule-item-time {
    font-weight: 600;
    padding: 0 0 0 16px;
    border-bottom: 1px solid $white;
  }

  &__schedule-item-title {
    padding: 0 0 0 16px;
  }

  &__schedule-item-button {
    width: 200px;
    margin: 48px 0 0 auto;
    padding: 14px;
    border-radius: 8px;
    color: $blue;
    text-decoration: none;
    border: unset;

    &:hover {
      color: $blue;
      background-color: $white;
      border: unset;
    }
  }
}

@media (max-width: $tablet-max-width) {
  .content {
    &__schedule-list {
      margin: 32px 0 0;
      justify-content: space-between;
    }

    &__schedule-item {
      width: 47%;
      margin: 0 0 24px;

      &:nth-child(2n) {
        margin: 0 0 24px 24px;
      }
    }

    &__schedule-item-time {
      padding: 0;
    }

    &__schedule-item-title {
      padding: 0;
    }

    &__schedule-item-button {
      width: 100%;
      margin: 32px 0 0;
      padding: 14px;
      border-radius: 8px;
      color: $blue;
      text-decoration: none;
      border: unset;

      &:hover {
        color: $blue;
        background-color: $white;
        border: unset;
      }
    }
  }
}

@media (max-width: $mobile-max-width) {
  .content {
    &__schedule-list {
      @include list;
      margin: 32px 0 0;
      display: flex;
      flex-direction: column;
    }

    &__schedule-item {
      width: 100%;
      margin: 0 0 24px;

      &:nth-child(2n) {
        margin: 0 0 24px;
      }
    }

    &__schedule-item-date {
      text-align: left;
    }

    &__schedule-item-time {
      padding: 0;
    }

    &__schedule-item-title {
      padding: 0;

    }

    &__schedule-item-button {
      width: 200px;
      margin: 32px 0 0 auto;
    }
  }
}
